<template>
  <div>
    Edit {{modele}}
    <b-container fluid>
      <b-row class="my-1" v-for="f in fields" :key="f.label">
        <b-col sm="3">
          <label :for="`field-${f.label}`"><code>{{ f.label }}</code>:</label>
        </b-col>
        <b-col sm="9">
          <b-form-select v-if="f.type=='select'" v-model="cat[f.label]" :options="f.options || options"></b-form-select>
          <b-form-textarea
          v-else-if="f.type=='textarea'"
          id="textarea"
          v-model="cat[f.label]"
          placeholder="..."
          rows="3"
          max-rows="6"
          ></b-form-textarea>
          <b-form-input v-else v-model="cat[f.label]" :id="`field-${f.label}`" :type="f.type"></b-form-input>
        </b-col>
      </b-row>
      <b-button @click="save">Save</b-button>
      <!-- {{ cats}}
      <hr>
      {{ options}} -->
    </b-container>
  </div>
</template>

<script>
export default {
  name: "Edit",
  data() {
    return {
      cat:null,
      fields: [
        {'label': 'name', type: 'text'},
        {'label': 'content', type: 'text'},
        {'label': 'age', type: 'number'},
        {'label': 'property', type: 'select', options: ["one", "two"]},
        {'label': 'link', type: 'select'},
        {'label': 'watch', type: 'select'},
        {'label': 'send', type: 'select'},
         {'label': 'action', type: 'textarea'},
        // 'text',
        // 'number',
        // 'email',
        // 'password',
        // 'search',
        // 'url',
        // 'tel',
        // 'date',
        // 'time',
        // 'range',
        // 'color'
      ],

    }
  },
  created() {
    console.log("params",this.$route.params.modele)
    this.modele = this.$route.params.modele
    console.log("route",this.$route)
    if(this.$route.params.cat) {
      this.cat = this.$route.params.cat;
    } else {
      this.cat = { name:'', age: 0 };
    }
  },
  methods: {
    async save() {
      console.log(this.cat)
      this.cat.modele = this.modele
      this.cat.updated = Date.now()
      await this.$store.dispatch('cats/saveCat', this.cat);
      console.log('back');
      this.$router.go(-1);
    }
  },
  computed: {
    cats() {
      return this.$store.state.cats.cats;
    },
    options() {
      let opts = this.$store.state.cats.cats.map(c => {return{value: c, text: c.name+" ("+c.modele+")"}})
      return opts;
    }
  },
}
</script>

